import { useNavigate } from "react-router-dom";
import classes from "./Welcome.module.css";

const Welcome = () => {
  const goTo = useNavigate();
  return (
    <>
      <div className={classes.container}>
        <h2 className={classes.title}>Witamy na pokadzie </h2>
        <p className={classes.para}>
          z nasz " List do zrobienia" nie zapomnisz niczego !{" "}
        </p>
        <p className={classes.para}>
          Wystarczy zaoy darmowe konto Aby cieszy si List{" "}
        </p>
        <button
          onClick={() => {
            goTo("/rejestracja");
          }}
          className={classes.button}
        >
          zarejestruj sie{" "}
        </button>
        <p className={classes.para}>
          ale jeli ju masz Konto TO zaloguj si !
        </p>
        <button
          onClick={() => {
            goTo("/login");
          }}
          className={classes.button}
        >
          zaloguj si{" "}
        </button>
      </div>
    </>
  );
};
export default Welcome;
